@font-face {
  font-family: "ComingSoon-Regular";
  src: url("../styles/font/ComingSoon-Regular.ttf");
  src: url("../styles/font/ComingSoon-Regular.ttf") format("ttf"),
    url("../styles/font/ComingSoon-Regular.ttf") format("truetype");
}

* {
  font-family: "Coming Soon", cursive;
}

.active .icon i {
  color: white !important;
}

.active .nav-link-text {
  color: #344767 !important;
}
.sidenav1 {
  display: none !important;
}

@media screen and (min-width: 1200px) {
  .sidenav1 {
    display: block !important;
  }
}

.offcanvas-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
}
.offcanvas-backdrop.show {
  opacity: 0.5 !important;
}

.offcanvas-start {
  width: 250px !important;
}
.nav-link.active .icon {
  background-image: linear-gradient(310deg, #cb0c9f 0%, #cb0c9f 100%);
}
.nav-link.active {
  background-color: #fff;
}
